<template>
  <article
    class="modal fade"
    id="exampleModalCenter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalCenterTitle"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalCenterTitle">新增產品</h5>
          <button
            type="button"
            @click="cleanTempData()"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <section class="modal-body">
          <form>
            <div class="form-row mb-5">
              <div class="col">
                名稱
                <input type="text" class="form-control" placeholder="名稱" v-model="tempData.title">
              </div>
              <div class="col">
                性別
                <select class="custom-select mr-sm-2" v-model="tempData.gender">
                  <option selected value="">請選擇...</option>
                  <option value="women">女性</option>
                </select>
              </div>
              <div class="col" v-if="isTempCategoryShow">
                種類
                <select class="custom-select mr-sm-2" v-model="tempData.category">
                  <option selected>請選擇...</option>
                  <option :value="item.id" v-for="item in tempCategory" :key="item.id">{{item.name}}</option>
                </select>
              </div>
              <div class="col">
                價格
                <input
                  type="number"
                  class="form-control"
                  placeholder="價格"
                  v-model="tempData.price"
                >
              </div>
            </div>
            <button class="btn btn-primary" @click.prevent="addEmptyColor">新增樣式</button>
            <section
              class="form-row my-5 border p-2"
              v-for="(color,index) in tempData.colors"
              :key="color.tempId"
              v-give-colorid="index"
            >
              <div class="col-sm-6 col-12 position-relative mb-sm-0 mb-2">
                <input
                  type="file"
                  class="filelisten btn btn-primary w-100 mb-2"
                  v-file-listen
                  accept=".jpg"
                >
                <img class="imglisten w-100">
              </div>
              <div class="col-sm-6 col-12">
                <div class="form-row mb-2">
                  <div class="col">樣式編號{{color.colorId}}</div>
                  <div class="col d-flex">
                    <button
                      class="btn btn-primary py-1 ml-auto"
                      @click.prevent="deleteColor(index)"
                    >X</button>
                  </div>
                </div>
                <div class="form-row mb-2">
                  <div class="col">
                    樣式色碼
                    <input
                      type="color"
                      class="form-control"
                      placeholder="色碼"
                      v-model="tempData['colors'][index]['colorNum']"
                    >
                  </div>
                  <div class="col">
                    樣式名稱
                    <input
                      type="text"
                      class="form-control"
                      placeholder="顏色名稱"
                      v-model="tempData['colors'][index]['colorName']"
                    >
                  </div>
                </div>
                <div class="form-row mb-2">
                  <div class="col">
                    請選擇尺寸
                    <div class="form-check form-check-inline ml-2">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="S"
                        :value="{sizeId:0,sizeName:'S'}"
                        v-model="tempData.colors[index]['sizes']"
                      >
                      <label class="form-check-label" for="S">S</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="M"
                        :value="{sizeId:1,sizeName:'M'}"
                        v-model="tempData.colors[index]['sizes']"
                      >
                      <label class="form-check-label" for="M">M</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="L"
                        :value="{sizeId:2,sizeName:'L'}"
                        v-model="tempData.colors[index]['sizes']"
                      >
                      <label class="form-check-label" for="L">L</label>
                    </div>
                  </div>
                </div>
              </div>
            </section>
            <section></section>
          </form>
        </section>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-dismiss="modal"
            @click="cleanTempData()"
          >關閉</button>
          <button type="button" class="btn btn-primary" @click="postProduct">儲存</button>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import $ from 'jquery'
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      isTempCategoryShow: false,
      colorCounter: 0,
      tempCategory: [],
      tempData: {
        title: "",
        gender: "",
        category: "",
        price: 0,
        colorImgNum: null,
        introductionImgNum: 0,
        globalImg: [],
        colors: []
      },
      emptyColor: {
        colorId: "",
        colorNum: "#000000",
        colorName: "黑色",
        sizes: []
      },
      emptyData: {
        title: "",
        gender: "",
        category: "",
        price: 0,
        colorImgNum: 1,
        introductionImgNum: 0,
        globalImg: [],
        colors: []
      },
      emptySize: {
        sizeNmae: "",
        sizeId: ""
      },
      files: []
    };
  },
  computed: {
    tempGender() {
      return this.tempData.gender;
    },
    dataColors() {
      return this.tempData.colors;
    }
  },
  watch: {
    tempGender(val, oldVal) {
      if (val) {
        this.getCategory();
      }
    },
    dataColors(val) {
      console.log(val);
      this.tempData.colorImgNum = val.length;
    }
  },
  methods: {
    deleteColor(colorIndex) {
      this.tempData.colors.splice(colorIndex, 1);
    },
    cleanTempData() {
      this.tempData = this.emptyData;
    },
    addEmptyColor() {
      const empty = Object.assign({}, this.emptyColor);
      empty.tempId = this.colorCounter;
      this.colorCounter += 1;
      this.tempData.colors.push(empty);
    },
    getCategory() {
      const getCategory = new AxiosClass(
        `admin/category_gender/${this.tempGender}`,
        "get",
        null,
        true
      );
      getCategory.doAxios(
        this,
        res => {
          for (const bigCategory of res.data.data) {
            this.tempCategory.push(...bigCategory.category);
            this.isTempCategoryShow = true;
            this.$parent.$emit("push-alert", "讀取種類成功");
          }
        },
        () => {
          this.$parent.$emit("push-alert", "讀取種類失敗", true);
        }
      );
    },
    postProduct() {
      this.tempData.colors.map(element => {
        element.sizes.sort((a, b) => {
          return a.sizeId - b.sizeId;
        });
      });
      delete this.tempData.tempId;
      let formData = new FormData();
      const data = JSON.stringify(this.tempData);
      formData.append("productData", data);
      let filesDOMList = document.querySelectorAll(".filelisten");
      for (let i = 0; i < filesDOMList.length; i++) {
        formData.append(`productImg${i + 1}`, filesDOMList[i]["files"][0]);
      }
      const axios = require("axios");
      axios({
        method: "post",
        url: process.env.VUE_APP_BACKURL + "admin/product",
        data: formData,
        config: { headers: { "Content-Type": "multipart/form-data" } },
        withCredentials: true
      })
        .then(()=>{
          this.cleanTempData();
          this.$parent.$emit("push-alert", "上傳成功");
          this.$parent.getProducts()
          $('#exampleModalCenter').modal('hide')
        })
        .catch(error => {
          this.$parent.$emit("push-alert", "上傳失敗", true);
        });
    }
  },
  directives: {
    "give-colorid": {
      bind: function(el, binding, vnode) {
        const colorSelf = vnode.context.tempData["colors"][binding.value];
        colorSelf["colorId"] = binding.value + 1;
      }
    },
    "file-listen": {
      bind: function(el) {
        el.addEventListener("change", function() {
          let reader = new FileReader();
          reader.addEventListener("load", function() {
            el.nextSibling.src = reader.result;
          });
          reader.readAsDataURL(el.files[0]);
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.imglisten {
}
</style>
